/******************* BOX   NEWS letter   ************/
#newsletter .control-label {
	margin-right: 5px;
	font-size: 16px;
}

#newsletter input {
	border: 1px solid $color-secondary;
	height: 35px;
}

.btn.btn-newsletter {
	border: 2px solid;
	background-color: $color-secondary;
	border-color: $color-secondary;
	color: white;
	height: 35px;
}

.btn.btn-newsletter:hover {
	background-color: white;
	border-color: $color-secondary;
	color: $color-secondary;
}
