html,
body {
  margin: 0;
  padding: 0;
}

a,
a:hover,
a:focus,
a:active,
a:visited {
  color: inherit;
  text-decoration: none;
  outline: none;
}

.row:after, .row:before {
  content: '';
  display: block;
}

.flex {
  display: flex;
  flex-wrap: wrap;
}

.align-center {
  align-items: center;
}

/*	colors	*/
/*	font families	*/
/*	navbar 	*/
/*	misc	*/
html,
body {
  font-family: "Open Sans", sans-serif;
  font-weight: normal;
  color: #303030;
  font-size: 13px;
}

body {
  overflow-x: hidden;
  margin-top: 80px;
}

main {
  padding-top: 50px;
  padding-bottom: 50px;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Raleway", sans-serif;
  font-weight: bold;
  color: #ff8040;
}

h1, .h1 {
  font-size: 28px;
  font-weight: bold;
}

h1.smally, .h1.smally {
  font-size: 17px;
  font-weight: bold;
}

h2, .h2 {
  font-size: 28px;
  color: #606060;
  font-weight: bold;
}

h2.color-6, .h2.color-6 {
  color: #FFe000;
}

h3, .h3 {
  font-size: 17px;
  font-weight: bold;
}

h4, .h4 {
  font-size: 15px;
  font-weight: bold;
}

body {
  font-size: 13px;
  line-height: 1.4;
  font-family: "Open Sans", sans-serif;
}

@media (min-width: 768px) {
  body {
    font-size: 14px;
  }
}

@media (min-width: 992px) {
  body {
    font-size: 15px;
  }
}

@media (min-width: 1200px) {
  body {
    font-size: 16px;
  }
}

h1 {
  font-size: 2em;
}

h2 {
  font-size: 1.7em;
}

h3 {
  font-size: 1.4em;
}

h4 {
  font-size: 1.2em;
}

h5 {
  font-size: 1.1em;
}

h6 {
  font-size: 1em;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Raleway", sans-serif;
  margin: 1em 0 0.5em;
}

h1:first-child,
h2:first-child,
h3:first-child,
h4:first-child,
h5:first-child,
h6:first-child {
  margin-top: 0;
}

ul, ol, p {
  margin-bottom: 0.5em;
}

ul, ol, p:last-child {
  margin-bottom: 0;
}

ul, ol {
  padding-left: 2em;
}

pre,
code {
  font-family: "Lucida Console", Monaco, monospace;
}

.navbar {
  font-family: "Raleway", sans-serif;
  background-color: white;
  color: #303030;
  padding-top: 15px;
  padding-bottom: 15px;
  border: 0;
}

.navbar .container .navbar-header {
  margin-left: 0;
  margin-right: 0;
}

.navbar .container .navbar-header .navbar-brand {
  margin-left: 0;
  height: auto;
  padding: 0;
  height: 50px;
}

.navbar .container .navbar-header .navbar-brand > img {
  height: 50px;
}

.navbar-nav > li > a {
  padding: 0 15px;
  line-height: 50px;
}

.navbar-nav > li .dropdown-menu {
  margin-top: 15px;
}

.nav > li.open > a,
.nav > li.open > a:hover,
.nav > li.open > a:focus {
  background-color: rgba(255, 128, 64, 0.5);
  color: white;
}

.nav > li.active > a {
  background-color: rgba(96, 96, 96, 0.5);
  color: white;
}

.nav > li a:hover,
.nav > li a:focus {
  background-color: #ff8040;
  color: white;
}

.navbar-fixed-top .navbar-collapse {
  max-height: 300px;
}

.navbar-toggle {
  padding: 0;
  line-height: 50px;
  width: 50px;
  margin: 0;
  border-radius: 0;
  border: 0;
  transition: all 0.25s ease-in-out;
}

.navbar-toggle:hover {
  background-color: #ff8040;
  color: white;
}

.dropdown-menu {
  border: 0;
  box-shadow: none;
  padding: 0;
  border-radius: 0;
}

.dropdown-menu > li > a {
  padding: 0 15px;
  line-height: 3em;
}

.call-action {
  float: right;
  margin-right: 10px;
  height: 50px;
  width: 50px;
  text-align: center;
  transition: all 0.25s ease-in-out;
}

.call-action > i {
  line-height: 50px;
}

.call-action:hover {
  background-color: #ff8040;
}

.call-action:hover > i {
  color: white;
}

.btn {
  font-weight: 600;
  border: 3px solid transparent;
  background: transparent;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  border-radius: 0;
  color: #303030;
  transition: all 0.25s ease-in-out;
}

.btn.active, .btn.focus, .btn:active, .btn:hover {
  color: #fff;
  border-color: #606060;
  text-decoration: none;
  outline: none;
  box-shadow: none;
}

.btn.btn-icon {
  background-color: #606060;
  border-color: #606060;
  border: 0;
  color: #fff;
}

.btn.btn-sm {
  font-weight: 400;
  border-width: 2px;
}

.btn.btn-lg {
  border-width: 5px;
}

.btn.with-icon {
  margin: 4px;
  padding-left: 10px;
  padding-right: 10px;
  width: 39px;
}

.btn.disabled,
.btn[disabled],
fieldset[disabled] .btn {
  cursor: not-allowed;
  pointer-events: none;
  opacity: 0.65;
  filter: alpha(opacity=65);
  box-shadow: none;
}

.btn-default,
a.btn-default {
  color: #303030;
  border-color: transparent;
  background-color: transparent;
}

.btn-default:active, .btn-default:focus, .btn-default:hover,
a.btn-default:active,
a.btn-default:focus,
a.btn-default:hover {
  color: #303030;
  border-color: #303030;
  background: white;
}

.btn-primary,
a.btn-primary {
  color: white;
  border-color: #ff8040;
  background-color: #ff8040;
}

.btn-primary:active, .btn-primary:focus, .btn-primary:hover,
a.btn-primary:active,
a.btn-primary:focus,
a.btn-primary:hover {
  color: #ff8040;
  border-color: #ff8040;
  background-color: white;
}

.btn-secondary,
a.btn-secondary {
  color: white;
  border-color: #606060;
  background-color: #606060;
}

.btn-secondary:active, .btn-secondary:focus, .btn-secondary:hover,
a.btn-secondary:active,
a.btn-secondary:focus,
a.btn-secondary:hover {
  color: #606060;
  border-color: #606060;
  background-color: white;
}

.btn-accent,
a.btn-accent {
  color: #FFe000;
  border-color: #FFe000;
  background-color: white;
}

.btn-accent:active, .btn-accent:focus, .btn-accent:hover,
a.btn-accent:active,
a.btn-accent:focus,
a.btn-accent:hover {
  color: #303030;
  border-color: #FFe000;
  background-color: #FFe000;
}

input::-moz-focus-inner {
  border: 0;
  padding: 0;
  margin-top: -3px;
  margin-bottom: -3px;
}

select:not([multiple]) {
  -webkit-appearance: none;
  -moz-appearance: none;
  background-position: right 50%;
  background-repeat: no-repeat;
  background-image: url(data:image/png;base64,);
  padding: .5em;
  padding-right: 1.5em;
  color: #9D9999;
}

/************************  HP SLIDER ************************/
.carousel-indicators {
  position: absolute;
  bottom: 0px;
}

.carousel-indicators li {
  background-color: #DADADA;
  border: 0px solid #FFFFFF;
  border-radius: 2px;
  cursor: pointer;
  display: inline-block;
  height: 20px;
  margin: 1px;
  text-indent: -999px;
  width: 20px;
}

.carousel-indicators .active {
  background-color: #A81729;
  height: 20px;
  margin: 1px;
  width: 20px;
}

.slider-side-box {
  padding: 25px;
}

.carousel-inner > .item > img {
  width: 100%;
}

.carousel-caption {
  position: absolute;
  left: 15%;
  right: 15%;
  top: 37%;
  z-index: 10;
  padding-top: 20px;
  padding-bottom: 20px;
  color: #fff;
  text-align: center;
  text-shadow: none;
  font-weight: normal;
}

@media (max-width: 767px) {
  .carousel-caption {
    top: 10%;
  }
}

.carousel-caption h2 {
  color: #FFFFFF;
  font-size: 3rem;
  /* Animation delays */
  animation-delay: 1s;
}

@media (max-width: 767px) {
  .carousel-caption h2 {
    font-size: 1rem;
  }
}

.carousel-caption p {
  color: #FFFFFF;
  font-size: 110%;
  animation-delay: 2s;
}

.carousel-control {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 15%;
  font-size: 20px;
  color: #fff;
  text-align: center;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.6);
  filter: alpha(opacity=50);
  opacity: .5;
}

.carousel-caption button {
  animation-delay: 3s;
}

/****EFFETTO FADE CAROUSEL BOOTSTRAP*****/
.carousel.carousel-fade .item {
  transition: opacity 0.5s ease-in-out;
  opacity: 0;
}

.carousel.carousel-fade .active.item {
  opacity: 1;
}

.carousel.carousel-fade .active.left,
.carousel.carousel-fade .active.right {
  left: 0;
  z-index: 2;
  opacity: 0;
  filter: alpha(opacity=0);
}

.carousel.carousel-fade .next,
.carousel.carousel-fade .prev {
  left: 0;
  z-index: 1;
}

.carousel.carousel-fade .carousel-control {
  z-index: 3;
}

.modal-content {
  position: relative;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #999;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 0px;
  outline: 0;
  box-shadow: 0 3px 9px rgba(0, 0, 0, 0.5);
}

/*******************************     THUMBNAIL        *******************************/
.thumbnail {
  background-color: #fff;
  border: 0px solid #ddd;
  border-radius: 0px;
  display: block;
  line-height: 1.42857;
  margin-bottom: 10px;
  padding: 0px;
  transition: border 0.2s ease-in-out 0s;
}

.thumbnail .caption {
  text-align: left;
  padding: 10px;
  min-height: 55px;
}

.caption-image {
  position: absolute;
  top: 0;
  right: 0;
  background: rgba(22, 193, 143, 0.7);
  width: 100%;
  height: 100%;
  text-align: center;
  color: #fff !important;
  z-index: 2;
  filter: alpha(opacity=0);
  -webkit-backface-visibility: hidden;
  transition: all 0.4s ease-in-out;
  transform: scale(0);
  padding: 52% 40%;
}

.box-image {
  position: relative;
}

.box-image:hover .caption-image {
  opacity: 0.8;
  transform: scale(1);
  filter: alpha(opacity=50);
}

.abs-footer-box {
  position: absolute;
  bottom: 15px;
  left: 15px;
  margin: 0px;
  right: 15px;
}

.product-gallery:hover img {
  opacity: 0.6;
  filter: alpha(opacity=60);
  /* For IE8 and earlier */
  background: red;
}

.product-gallery .caption,
.servizi-gallery .caption {
  text-align: left;
  padding: 10px 25px 5px 25px;
  min-height: 80px;
}

.product-gallery .zoom-image {
  position: absolute;
  right: 0;
  width: 100%;
  height: 100%;
  text-align: center;
  color: #fff !important;
  z-index: 2;
  filter: alpha(opacity=0);
  -webkit-backface-visibility: hidden;
  transition: all 0.4s ease-in-out;
  transform: scale(0);
  opacity: 0.6;
  top: 29%;
  bottom: 0px;
  filter: alpha(opacity=60);
  /* For IE8 and earlier */
}

.product-gallery:hover .zoom-image {
  opacity: 0.9;
  transform: scale(1);
  filter: alpha(opacity=90);
}

.item-badge {
  padding-top: 40px;
}

.box-badge {
  padding-top: 40px;
}

.box-badge img {
  margin-top: -80px;
}

/*Fancybox
 ------------------------------------*/
/*For Topbar*/
.fancybox-overlay.fancybox-overlay-fixed {
  z-index: 9999;
}

/*FancyBox Hover Effect*/
.zoomer:hover .overlay-zoom .zoom-icon {
  opacity: 0.6;
  transform: scale(1);
  filter: alpha(opacity=50);
}

.overlay-zoom {
  z-index: 90;
  width: 100%;
  height: 100%;
  display: block;
  cursor: pointer;
  position: relative;
  transition: all 0.1s ease-in-out;
}

.overlay-zoom .zoom-icon {
  top: 0;
  opacity: 0;
  z-index: 100;
  width: inherit;
  height: inherit;
  position: absolute;
  background-color: #000;
  background-position: 50%;
  background-repeat: no-repeat;
  background: rgba(9, 147, 187, 0.75);
  filter: alpha(opacity=0);
  -webkit-backface-visibility: hidden;
  transition: all 0.4s ease-in-out;
  transform: scale(0);
  left: 0;
  right: 0;
  padding: 24% 44%;
}

.team-gallery .overlay-zoom .zoom-icon {
  padding: 48% 43%;
}

@media (max-width: 768px) {
  .fancybox-overlay {
    background: none !important;
  }
}

/***********  PAGER  ******************/
.pagination {
  border-radius: 0px;
  display: inline-block;
  margin: 10px 0 0px;
  padding-left: 0;
}

/***********  mappa  ******************/
.map {
  width: 100%;
  height: 600px;
  border-top: solid 1px #eee;
  border-bottom: solid 1px #eee;
}

.gm-style-iw {
  /*overflow: inherit !important;*/
}

.info-title {
  width: 250px;
}

#contact-layer {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0px;
  opacity: 0.8;
  filter: alpha(opacity=80);
  vertical-align: middle;
  display: table-cell;
  padding: 10% 0px 0px 0px;
  line-height: 4em;
}

.showMap {
  position: absolute;
  z-index: 3;
  top: 20px;
  right: 20px;
}

/*******************************  TOP CONTROL ***************************************/
#topcontrol {
  z-index: 10000;
}

/*******************************    TABLE CONTENT     *******************************/
.table-content {
  display: table;
  width: 100%;
  margin: 0;
}

.table-row {
  display: table-row;
}

.table-cell {
  display: table-cell;
  vertical-align: middle;
}

.table-cell * {
  margin: 0;
}

/******************* BOX   NEWS letter   ************/
#newsletter .control-label {
  margin-right: 5px;
  font-size: 16px;
}

#newsletter input {
  border: 1px solid #606060;
  height: 35px;
}

.btn.btn-newsletter {
  border: 2px solid;
  background-color: #606060;
  border-color: #606060;
  color: white;
  height: 35px;
}

.btn.btn-newsletter:hover {
  background-color: white;
  border-color: #606060;
  color: #606060;
}

/* ====== ELEMENTS ======= */
.socialIcons {
  text-align: right;
}

.socialIcons > li > a {
  height: 35px;
  width: 35px;
  display: inline-block;
  text-align: center;
  color: white;
  background-color: #ff8040;
  border-radius: 50%;
  transition: all 0.25s ease-in-out;
}

.socialIcons > li > a > i {
  line-height: 35px;
}

.socialIcons > li > a:hover {
  text-decoration: none;
  background-color: #FFe000;
  color: #303030;
}

/* ====== ELEMENTS ======= */
#news-sharer {
  clear: both;
  margin-top: 10px;
  padding: 10px 0;
  text-align: right;
}

#news-sharer a {
  font-size: 0;
}

#news-sharer a i {
  border-radius: 50%;
  display: inline-block;
  font-size: 18px;
  height: 30px;
  line-height: 30px;
  margin: 5px;
  text-align: center;
  transition: all 0.3s ease 0s;
  width: 30px;
  color: white;
  background-color: #606060 !important;
}

#news-sharer a:hover i {
  height: 40px;
  line-height: 40px;
  margin: 0;
  width: 40px;
}

#news-sharer .facebook i:hover,
#news-sharer .like-on-facebook i:hover {
  background-color: #3b5998 !important;
}

#news-sharer .twitter i:hover {
  background-color: #00aced !important;
}

#news-sharer .google-1 i:hover,
#news-sharer .google-plus i:hover {
  background-color: #dd4b39 !important;
}

#news-sharer .linkedin i:hover {
  background-color: #007bb5 !important;
}

#news-sharer .pinterest i:hover {
  background-color: #cb2027 !important;
}

#carousel .slide {
  position: relative;
}

#carousel .slide img {
  width: 100%;
  max-width: auto;
}

#carousel .slide .caption {
  padding: 50px;
  background-color: white;
}

@media (min-width: 992px) {
  #carousel .slide .caption {
    padding: 0;
    background-color: transparent;
    position: absolute;
    top: 30%;
    bottom: 20%;
    left: 20%;
    right: 50%;
  }
}

