html,
body {
	font-family: $font-family-text;
	font-weight: normal;
	color: $color-text;
	font-size: 13px;
}
body {
	overflow-x: hidden;
	margin-top: $navbar-height;
}

main {
	padding-top: 50px;
	padding-bottom: 50px;
}

h1,h2,h3,h4,h5,h6 {
	font-family: $font-family-title;
	font-weight: bold;
	color: $color-primary;
}

h1,.h1 {
	font-size: 28px;
	font-weight: bold;
	&.smally {
		font-size: 17px;
		font-weight: bold;
	}
}

h2,.h2 {
	font-size: 28px;
	color: $color-secondary;
	font-weight: bold;
	&.color-6 {
		color: $color-accent;
	}
}

h3,.h3 {
	font-size: 17px;
	font-weight: bold;
}

h4,.h4 {
	font-size: 15px;
	font-weight: bold;
}
