input::-moz-focus-inner {
  border: 0;
  padding: 0;
  margin-top: -3px;
  margin-bottom: -3px;
}

select:not([multiple]) {
  -webkit-appearance: none;
  -moz-appearance: none;
  background-position: right 50%;
  background-repeat: no-repeat;
  background-image: url(data:image/png;base64,);
  padding: .5em;
  padding-right: 1.5em;
  color: #9D9999;
}